<template>
  <div>
    <div class="tree-demo">
      <div class="btn-arr">
        <button @click="getCheckNodes">获取选中的节点</button>
        <button @click="getCheckKeys">获取选中节点的id</button>
      </div>
      <tree
        ref='tree'
        :treeData="treeData"
        :showFoldIcon="true"
        :showCheckBox="true"
        :defaultCheckedKeys="['0']"
        :defaultExpendKeys="['0']"
        :defaultDisabledKeys="['1']"
        :renderContent="renderContent"
        class="tree"
        @checkChanged="checkChanged"
        @nodeClick="nodeClick"/>
    </div>
  </div>
</template>

<script>
import eventUtil from '@/assets/js/eventUtil.js'
import Tree  from 'tree-custom' // 方法2
export default {
  name: 'App',
  components: {
    Tree
  },
  data() {
    return {
      treeData: []
    }
  },
  mounted() {
    this.initTreeData([2, 1])
  },
  methods: {
    renderContent(h, { node }) {
      return (
        <span class="custom-tree-node">
          <span class="tree-text" >{node.label}</span>
          <span class="tree-node-btn">
            <button on-click={ (event) => this.addNode(event, node) }>Add</button>
            <button on-click={ (event) => this.delNode(event, node) }>Del</button>
          </span>
        </span>
      );
    },
    initTreeData(arr) {
      const setChildren = (obj, arr, index) => {
        if (index >= arr.length) return;
        for (let j = 0; j < arr[index]; j++) {
          let id = obj.id + "_" + j;
          let child = {
            id: id,
            label: "子级" + id,
            children: [],
          };
          setChildren(child, arr, index + 1);
          obj.children.push(child);
        }
      };
      if (arr.length < 1) return;
      for (let i = 0; i < arr[0]; i++) {
        let obj = {
          id: i.toString(),
          label: "父级" + i,
          children: [],
        };
        setChildren(obj, arr, 1);
        this.treeData.push(obj);
      }
    },
    checkChanged(checked, arr) {
      console.log(checked, arr);
    },
    getCheckNodes() {
      let arr = this.$refs.tree.getCheckedNodes().map( item => item.label);
      alert(arr.join(','));
    },
    getCheckKeys() {
      let arr = this.$refs.tree.getCheckedKeys();
      alert(arr.join(','));
    },
    nodeClick(opend, node) {
      let status = opend ? '节点打开' : '节点关闭';
      console.log(node.label + ':' + status);
    },
    addNode(event, node) {
      eventUtil.stopPropagation(event);
      let id = node.id + "_" + node.children.length;
      let newNode = {
        id: id,
        label: "子级" + id,
        children: [],
        checked: node.checked,
        parent: node,
      };
      node.children.push(newNode);
    },
    delNode(event, node) {
      eventUtil.stopPropagation(event);
      const children = node.parent.children;
      const index = children.findIndex((d) => d.id === node.id);
      children.splice(index, 1);
    },
  }
}
</script>

